:root {
  --font-primary: "Anton", sans-serif;
  --font-secondary: "Poppins", sans-serif;
  --font-accent: "Work Sans", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --bg-main: hsl(210, 11%, 96%);
  --bg-odd: hsl(129, 40%, 60%);
  --bg-even: hsl(87, 62%, 67%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}
html:focus-within {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  background-color: var(--bg-main);
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  min-height: 100vh;
  max-width: 90rem;
  margin: 2rem auto;
}

.btn-grad {
  background-image: linear-gradient(to right, #134e5e 0%, #71b280 51%, #134e5e 100%);
}

.btn-grad {
  display: block;
  min-width: 90%;
  margin-top: 3rem;
  padding: 0.5rem 0;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 2rem;
  font-family: var(--font-secondary);
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #ecc3c3;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.btn-grad:focus-within {
  outline: 3px dotted green;
}

.btn-grad:hover {
  background-position: right center;
  /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

@keyframes rotate-forever {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-spinner {
  animation-duration: 0.75s;
  animation-iteration-count: infinite;
  animation-name: rotate-forever;
  animation-timing-function: linear;
  height: 20px;
  width: 20px;
  border: 2px solid #e7dfdf;
  border-right-color: transparent;
  border-radius: 50%;
  display: inline-block;
}

footer {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 1;
}
footer .footer-content {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 5rem;
  background-color: #e7dfdf;
}
@media (max-width: 599px) {
  footer .footer-content {
    flex-direction: column;
  }
  footer .footer-content a:focus-within {
    outline: 3px dotted green;
  }
  footer .footer-content img {
    transform: scale(0.7);
  }
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100vw;
  background-color: var(--bg-odd);
}
nav > * {
  margin: 0 2rem;
}
@media (max-width: 599px) {
  nav .nav-logo {
    width: 200px;
    height: 50px;
  }
}
nav .links {
  margin-left: auto;
}
@media (max-width: 599px) {
  nav .links {
    display: none;
  }
}
@media (min-width: 600px) {
  nav .links {
    display: none;
  }
}
@media (min-width: 900px) {
  nav .links {
    display: flex;
  }
}
nav .links a:hover, nav .links a:focus-within {
  opacity: 0.8;
  outline: 3px dotted green;
}
nav .links a:first-of-type {
  background-color: var(--bg-even);
  background-image: url(../app/assets/bx-link-external.svg);
  background-position-x: 18ch;
  background-position-y: center;
  background-repeat: no-repeat;
  background-size: 15%;
  margin-right: 1rem;
  padding: 1rem 3rem;
  border-radius: 10px;
  color: #fff;
}
nav .links a:last-child {
  background-color: var(--bg-even);
  background-image: url(../app/assets/bxl-github.svg);
  background-position-x: 13ch;
  background-position-y: center;
  background-repeat: no-repeat;
  background-size: 20%;
  padding: 1rem 3rem;
  margin-left: 1rem;
  border-radius: 10px;
  color: #fff;
}
nav .burger-menu-icon {
  cursor: pointer;
}
nav .burger-menu-icon a:focus {
  outline: 3px dotted red;
}
@media (min-width: 900px) {
  nav .burger-menu-icon {
    display: none;
  }
}
nav .burger-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  height: 30%;
  width: 90%;
  position: absolute;
  top: 20%;
  right: 50%;
  transform: translateX(50%);
  background-color: #fff;
  border-radius: 10px;
}
nav .burger-menu.hide {
  display: none;
}
nav .burger-menu a {
  margin: 1rem auto;
}
nav .burger-menu a:first-of-type {
  background-color: var(--bg-even);
  background-image: url(../app/assets/bx-link-external.svg);
  background-position-x: 18ch;
  background-position-y: center;
  background-repeat: no-repeat;
  background-size: 15%;
  padding: 1rem 3rem;
  border-radius: 10px;
  color: #fff;
}
nav .burger-menu a:last-child {
  background-color: var(--bg-even);
  background-image: url(../app/assets/bxl-github.svg);
  background-position-x: 13ch;
  background-position-y: center;
  background-repeat: no-repeat;
  background-size: 20%;
  padding: 1rem 3rem;
  border-radius: 10px;
  color: #fff;
}
nav .burger-menu a:focus {
  outline: 3px dotted red;
}

header {
  display: none;
}
@media (min-width: 1200px) {
  header {
    display: flex;
    flex-direction: column;
    justify-content: end;
    grid-area: header;
    background-image: url(../app/assets/chucknorris.png);
    background-color: transparent;
    background-position: top center;
    background-size: 90%;
    background-clip: border-box;
    background-repeat: no-repeat;
    text-align: center;
  }
}
header #intro {
  display: none;
}
@media (min-width: 1200px) {
  header #intro {
    display: block;
    background-color: #e7dfdf;
    width: 90%;
    margin: 2rem auto;
    padding: 0 1rem;
    font-size: 1rem;
    color: #000;
    font-style: italic;
    list-style: none;
    border-radius: 10px;
  }
}

.jokelist {
  grid-area: jokelist;
  margin: 0;
  min-height: 1rem;
  border-radius: 10px;
  overflow: scroll;
  color: #000;
}
.jokelist li {
  margin: 1rem 2rem;
  padding: 1rem;
  border-radius: 10px;
  font-weight: 600;
}
.jokelist li:nth-child(odd) {
  background-color: var(--bg-odd);
  color: #fff;
}
.jokelist li:nth-child(even) {
  background-color: var(--bg-even);
  color: #fff;
}

h1 {
  font-size: 3rem;
  font-family: var(--font-primary);
  text-align: center;
  letter-spacing: 3px;
}
@media (max-width: 599px) {
  h1 {
    font-size: 1.2rem;
    font-weight: 100;
    margin: 1rem;
  }
}

.disclaimer {
  color: #101110;
  font-weight: 400;
  font-family: var(--font-accent);
  font-size: 1.5rem;
  margin: 0 2rem;
}
@media (max-width: 599px) {
  .disclaimer {
    font-size: 1rem;
    line-height: 1;
  }
}

main {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  grid-area: main;
  background-color: #000;
  color: #fff;
  border-radius: 10px;
  margin: 0 auto;
  font-family: var(--font-accent);
}
main > * {
  margin: 1rem;
}
main .steps {
  color: yellow;
  text-align: center;
}
main .steps:first-child {
  margin-top: 3rem;
}
main .info {
  color: red;
  text-align: center;
  font-size: 0.8rem;
}
main .picknumber {
  text-align: center;
}
main .picknumber > * {
  margin: 1rem auto;
}
main .picknumber input[type=number] {
  display: block;
  width: 70%;
  text-align: center;
  font-size: 1.5rem;
}
main .picknumber input[type=number]:focus-within {
  outline: 3px dotted green;
}
main .picknumber input[type=checkbox] {
  transform: scale(1.5);
}
main .picknumber input[type=checkbox]:focus-within {
  outline: 3px dotted green;
}

#ticks {
  text-align: center;
}

.wrapper {
  display: grid;
  grid-template-columns: minmax(19.375rem, 1fr);
  grid-template-areas: "main" "jokelist";
}
@media (min-width: 600px) {
  .wrapper {
    justify-content: center;
    grid-template-columns: minmax(23.4375rem, 1fr);
    grid-template-rows: 37.5rem auto;
    grid-template-areas: "main" "jokelist";
  }
}
@media (min-width: 1200px) {
  .wrapper {
    grid-template-columns: repeat(3, minmax(23.4375rem, 1fr));
    grid-template-rows: 37.5rem;
    grid-template-areas: "header main jokelist";
    gap: 0.5rem;
  }
}